// ============================================
// CSV Import API Route with AI Field Mapping
// ============================================

import { NextRequest, NextResponse } from 'next/server'

interface CSVFieldMapping {
  csvColumn: string
  risivoField: string
  confidence: number
  transform?: 'none' | 'lowercase' | 'uppercase' | 'phone_format' | 'date_format'
}

interface CSVPreviewData {
  headers: string[]
  rows: Record<string, string>[]
  totalRows: number
  suggestedMappings: CSVFieldMapping[]
}

// RISIVO field definitions for mapping
const RISIVO_CONTACT_FIELDS = [
  { key: 'firstName', label: 'First Name', type: 'text', aliases: ['first name', 'fname', 'first', 'given name'] },
  { key: 'lastName', label: 'Last Name', type: 'text', aliases: ['last name', 'lname', 'last', 'surname', 'family name'] },
  { key: 'email', label: 'Email', type: 'email', aliases: ['email', 'e-mail', 'email address', 'mail'] },
  { key: 'phone', label: 'Phone', type: 'phone', aliases: ['phone', 'phone number', 'mobile', 'cell', 'telephone', 'tel'] },
  { key: 'company', label: 'Company', type: 'text', aliases: ['company', 'company name', 'organization', 'org', 'business'] },
  { key: 'jobTitle', label: 'Job Title', type: 'text', aliases: ['job title', 'title', 'position', 'role', 'job'] },
  { key: 'address', label: 'Address', type: 'text', aliases: ['address', 'street', 'address1', 'street address'] },
  { key: 'city', label: 'City', type: 'text', aliases: ['city', 'town'] },
  { key: 'state', label: 'State', type: 'text', aliases: ['state', 'province', 'region'] },
  { key: 'country', label: 'Country', type: 'text', aliases: ['country', 'nation'] },
  { key: 'zipCode', label: 'Zip Code', type: 'text', aliases: ['zip', 'zip code', 'postal code', 'postcode', 'zipcode'] },
  { key: 'website', label: 'Website', type: 'url', aliases: ['website', 'web', 'url', 'site', 'homepage'] },
  { key: 'dateOfBirth', label: 'Date of Birth', type: 'date', aliases: ['dob', 'date of birth', 'birthday', 'birth date'] },
  { key: 'leadSource', label: 'Lead Source', type: 'text', aliases: ['source', 'lead source', 'how did you hear', 'referral'] },
  { key: 'tags', label: 'Tags', type: 'tags', aliases: ['tags', 'labels', 'categories'] },
  { key: 'notes', label: 'Notes', type: 'textarea', aliases: ['notes', 'comments', 'description', 'remarks'] },
]

/**
 * AI-powered field mapping suggestion
 */
function suggestFieldMapping(csvHeader: string): CSVFieldMapping | null {
  const normalizedHeader = csvHeader.toLowerCase().trim()
  
  for (const field of RISIVO_CONTACT_FIELDS) {
    // Exact match
    if (field.key.toLowerCase() === normalizedHeader || field.label.toLowerCase() === normalizedHeader) {
      return {
        csvColumn: csvHeader,
        risivoField: field.key,
        confidence: 1.0,
        transform: field.type === 'email' ? 'lowercase' : field.type === 'phone' ? 'phone_format' : field.type === 'date' ? 'date_format' : 'none',
      }
    }
    
    // Alias match
    for (const alias of field.aliases) {
      if (alias === normalizedHeader || normalizedHeader.includes(alias)) {
        return {
          csvColumn: csvHeader,
          risivoField: field.key,
          confidence: 0.9,
          transform: field.type === 'email' ? 'lowercase' : field.type === 'phone' ? 'phone_format' : field.type === 'date' ? 'date_format' : 'none',
        }
      }
    }
    
    // Fuzzy match using similarity
    const similarity = calculateSimilarity(normalizedHeader, field.key.toLowerCase())
    if (similarity > 0.7) {
      return {
        csvColumn: csvHeader,
        risivoField: field.key,
        confidence: similarity,
        transform: 'none',
      }
    }
  }
  
  return null
}

/**
 * Calculate string similarity (Levenshtein-based)
 */
function calculateSimilarity(str1: string, str2: string): number {
  const longer = str1.length > str2.length ? str1 : str2
  const shorter = str1.length > str2.length ? str2 : str1
  
  if (longer.length === 0) return 1.0
  
  const editDistance = levenshteinDistance(longer, shorter)
  return (longer.length - editDistance) / longer.length
}

function levenshteinDistance(str1: string, str2: string): number {
  const matrix: number[][] = []
  
  for (let i = 0; i <= str1.length; i++) {
    matrix[i] = [i]
  }
  
  for (let j = 0; j <= str2.length; j++) {
    matrix[0][j] = j
  }
  
  for (let i = 1; i <= str1.length; i++) {
    for (let j = 1; j <= str2.length; j++) {
      const cost = str1[i - 1] === str2[j - 1] ? 0 : 1
      matrix[i][j] = Math.min(
        matrix[i - 1][j] + 1,
        matrix[i][j - 1] + 1,
        matrix[i - 1][j - 1] + cost
      )
    }
  }
  
  return matrix[str1.length][str2.length]
}

/**
 * Parse CSV content
 */
function parseCSV(content: string): { headers: string[]; rows: Record<string, string>[] } {
  const lines = content.split(/\r?\n/).filter(line => line.trim())
  
  if (lines.length === 0) {
    return { headers: [], rows: [] }
  }
  
  // Parse header line
  const headers = parseCSVLine(lines[0])
  
  // Parse data rows
  const rows: Record<string, string>[] = []
  for (let i = 1; i < lines.length; i++) {
    const values = parseCSVLine(lines[i])
    const row: Record<string, string> = {}
    
    headers.forEach((header, index) => {
      row[header] = values[index] || ''
    })
    
    rows.push(row)
  }
  
  return { headers, rows }
}

function parseCSVLine(line: string): string[] {
  const result: string[] = []
  let current = ''
  let inQuotes = false
  
  for (let i = 0; i < line.length; i++) {
    const char = line[i]
    
    if (char === '"') {
      if (inQuotes && line[i + 1] === '"') {
        current += '"'
        i++
      } else {
        inQuotes = !inQuotes
      }
    } else if (char === ',' && !inQuotes) {
      result.push(current.trim())
      current = ''
    } else {
      current += char
    }
  }
  
  result.push(current.trim())
  return result
}

/**
 * Apply field transformations
 */
function transformValue(value: string, transform?: string): string {
  if (!value) return value
  
  switch (transform) {
    case 'lowercase':
      return value.toLowerCase()
    case 'uppercase':
      return value.toUpperCase()
    case 'phone_format':
      const cleaned = value.replace(/[^\d+]/g, '')
      if (cleaned.length === 10) {
        return `+1 (${cleaned.slice(0, 3)}) ${cleaned.slice(3, 6)}-${cleaned.slice(6)}`
      }
      return value
    case 'date_format':
      try {
        return new Date(value).toISOString()
      } catch {
        return value
      }
    default:
      return value
  }
}

/**
 * POST /api/v1/import/csv
 * Processes CSV file upload and provides field mapping suggestions
 */
export async function POST(request: NextRequest) {
  try {
    const formData = await request.formData()
    const file = formData.get('file') as File | null
    const subAccountId = formData.get('subAccountId') as string | null
    const action = formData.get('action') as string | null

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // Preview action - parse CSV and suggest mappings
    if (action === 'preview' && file) {
      const content = await file.text()
      const { headers, rows } = parseCSV(content)
      
      // Generate AI-powered field mapping suggestions
      const suggestedMappings: CSVFieldMapping[] = []
      for (const header of headers) {
        const mapping = suggestFieldMapping(header)
        if (mapping) {
          suggestedMappings.push(mapping)
        } else {
          suggestedMappings.push({
            csvColumn: header,
            risivoField: '', // Unmapped
            confidence: 0,
          })
        }
      }
      
      // Return preview data (first 10 rows)
      const previewData: CSVPreviewData = {
        headers,
        rows: rows.slice(0, 10),
        totalRows: rows.length,
        suggestedMappings,
      }
      
      return NextResponse.json({
        success: true,
        data: previewData,
      })
    }

    // Import action - process the import with provided mappings
    if (action === 'import' && file) {
      const content = await file.text()
      const mappingsStr = formData.get('mappings') as string
      const mappings: CSVFieldMapping[] = mappingsStr ? JSON.parse(mappingsStr) : []
      const skipDuplicates = formData.get('skipDuplicates') === 'true'
      const mergeExisting = formData.get('mergeExisting') === 'true'
      
      const { headers, rows } = parseCSV(content)
      
      let imported = 0
      let skipped = 0
      let errors = 0
      const errorDetails: { row: number; error: string }[] = []
      
      for (let i = 0; i < rows.length; i++) {
        const row = rows[i]
        
        try {
          // Transform row data according to mappings
          const contactData: Record<string, any> = {
            subAccountId,
          }
          
          for (const mapping of mappings) {
            if (mapping.risivoField && row[mapping.csvColumn]) {
              contactData[mapping.risivoField] = transformValue(
                row[mapping.csvColumn],
                mapping.transform
              )
            }
          }
          
          // Skip if no email (required field)
          if (!contactData.email) {
            skipped++
            continue
          }
          
          // In production, this would create/update contacts via the contacts API
          // For now, we'll simulate the import
          imported++
          
        } catch (error) {
          errors++
          errorDetails.push({
            row: i + 2, // +2 for 1-indexed and header row
            error: error instanceof Error ? error.message : 'Unknown error',
          })
        }
      }
      
      return NextResponse.json({
        success: true,
        data: {
          imported,
          skipped,
          errors,
          totalRows: rows.length,
          errorDetails: errorDetails.slice(0, 10), // Return first 10 errors
          completedAt: new Date().toISOString(),
        },
      })
    }

    return NextResponse.json(
      { success: false, error: 'Invalid action. Use "preview" or "import"' },
      { status: 400 }
    )
  } catch (error) {
    console.error('CSV Import Error:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'CSV import failed' 
      },
      { status: 500 }
    )
  }
}

/**
 * GET /api/v1/import/csv
 * Returns available fields for mapping
 */
export async function GET() {
  return NextResponse.json({
    success: true,
    data: {
      fields: RISIVO_CONTACT_FIELDS.map(f => ({
        key: f.key,
        label: f.label,
        type: f.type,
        aliases: f.aliases,
      })),
      supportedFormats: ['csv', 'txt'],
      maxFileSize: '50MB',
      maxRows: 50000,
    },
  })
}
